  max-width: 120rem;
  margin: auto;
  display: block;
  overflow: hidden;
}

.footer .wide.top {
  max-width: 100%;
}

.footer .twenty {
  display: none;
}

.footer .top {
  background: #000000;
}

.footer .top .left {
  padding-left: 25px;
  overflow: hidden;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 21rem;
}

.footer .top .left li {
  float: left;
  margin-right: 1rem;
}

.footer .top .right {
  margin-left: auto;
  margin-right: auto;
  width: 28rem;
}

.footer .top .right form {
  padding-top: .3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.footer .top .right form .icon {
  float: left;
  margin-right: 1.5rem;
  font-size: 2.5rem;
}

.footer .top .right form input {
  float: left;
  margin-right: 5px;
  height: 3rem;
  border: 0;
  padding: 0 1rem;
  width: 68%;
  max-width: 300px;
}

.footer .top .right form button {
  height: 3rem;
  padding: 0;
  width: 3.8rem;
  border: 0;
}

.footer .column {
  padding-top: 20px;
  padding-left: 25px;
  text-decoration: none;
}

.footer .column li {
  margin-bottom: .7rem;
}

.footer .column a {
  font-size: 1.5rem;
  text-decoration: none;
}

.footer .column a:hover {
  text-decoration: underline;
}

.footer .column .tube {
  display: block;
  text-align: center;
  border-top: 1px solid #CCCCCC;
  border-bottom: 1px solid #CCCCCC;
  margin: -1px 2rem 0 0;
  padding: 1rem 0 1rem 0;
}

.footer .column .tube a {
  font-size: 1.6rem;
  display: inline-block;
}

.footer .center {
  text-align: center;
}

.footer .center p {
  margin: 2rem auto;
  width: 30rem;
  padding-top: 1rem;
  border-top: 1px solid #CCCCCC;
}

.footer .icon {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
}

.footer .facebook :hover {
  color: #2C4386;
  text-decoration: none;
}

.footer .twitter :hover {
  color: #0097EF;
  text-decoration: none;
}

.footer .pintrest :hover {
  color: #D9001B;
  text-decoration: none;
}

.footer .youtube :hover {
  color: #D9001B;
  text-decoration: none;
}

.footer .instagram :hover {
  color: #2C4386;
  text-decoration: none;
}

.footer .googleplus :hover {
  color: #D9001B;
  text-decoration: none;
}

@media only screen and (min-width: 480px) {
  .footer .column {
    float: left;
    width: 50%;
    height: 24rem;
  }
}

@media only screen and (min-width: 768px) {
  .footer .top .left {
    width: 40%;
    float: left;
    margin-left: 0;
    margin-right: 0;
  }
  .footer .top .right {
    float: left;
    width: 60%;
    margin-left: 0;
    margin-right: 0;
  }
  .footer .top .right form {
    padding-right: 25px;
    float: right;
  }
  .footer .column {
    float: left;
    width: 50%;
    height: 24rem;
  }
  .footer .column__tube {
    width: 100%;
  }
  .footer .eighty {
    width: 70%;
    float: left;
  }
  .footer .twenty {
    float: left;
    display: block;
    width: 30%;
  }
}

@media only screen and (min-width: 1025px) {
  .footer .eighty {
    width: 80%;
  }
  .footer .twenty {
    width: 20%;
  }
  .footer .column {
    float: left;
    width: 25%;
    height: 24rem;
  }
  .footer .column__tube {
    width: 100%;
  }
}



