.nav__featured {

  @extend %featured-content;

  .nav__content-group {

    > li,
    > div {

      // scss-lint:disable SelectorDepth NestingDepth
      a {
        text-decoration: none;
        text-transform: uppercase;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

}



