@mixin span-columns($columns) {
  flex-grow: $columns;

  .nav__content-group {
    display: flex;

    > li,
    > div {
      margin-right: $spacing-medium;
      flex: 1;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}



